import { gsap } from 'gsap';

class BannerAnimation {
  constructor() {
    this.banner = document.querySelectorAll('.service-banner');
    this.icon = document.querySelectorAll('.service-banner__icon');
  }

  animation(el, isForward) {
    if (isForward) {
      el.classList.add('is-animation');
    } else {
      el.classList.remove('is-animation');
    }
  }

  intersectionObserver() {
    const duration = 0.5;

    const isVisible = (entries) => {
      entries.forEach(entry => {
        const { isIntersecting } = entry;

        gsap.killTweensOf(this.icon);

        if (isIntersecting) {
          gsap.to(this.icon, { y: -50, rotate: 10, duration });
        } else {
          gsap.to(this.icon, { y: 0, rotate: 0, duration });
        }
      });
    };

    return new IntersectionObserver(isVisible, { threshold: 0.8 });
  }

  init() {
    this.observer = this.intersectionObserver();

    this.banner.forEach(title => {
      this.observer.observe(title);
    });
  }
}

export default BannerAnimation;